// 0axios
import axios from 'axios'

// 1axios
axios.defaults.timeout = 5000; //5
axios.defaults.baseURL = ''; //base url https://slot.ccfly.cc http://192.168.1.130:8090 https://testapiadmin.ccfly.cc/
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'; //postheader

// 2axios
const service = axios.create({
	timeout: 60000
})

// 3
service.interceptors.request.use(config => {
	// 
	let token = sessionStorage.getItem('token')
	if (token) { // tokenhttp headertoken
		config.headers = {
			'X-token': token
		}
	}
	return config
}, error => {
	console.log("1");
	return Promise.reject(error)
})

// 4
service.interceptors.response.use(response => {
	// 200
	// 
	if (response.status === 200) {
		if (response.data.code === 511) {
			// 
		} else if (response.data.code === 510) {
			// 
		} else {
			return Promise.resolve(response)
		}
	} else {
		return Promise.reject(response)
	}
}, error => {
	// 
	if (error.response.status) {
		// 
		// 
		return Promise.reject(error.response)
	}
})

// 5
export default service